@font-face {
  font-family: "Oswald Medium";

  font-display: swap;

  src:
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
}

@font-face {
  font-family: "Oswald Regular";

  font-display: swap;

  src:
    url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
}

@font-medium: "Oswald Medium", "Arial", sans-serif;
@font-regular: "Oswald Regular", "Arial", sans-serif;
@font-add: "Arial", sans-serif;
